.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Nav */
.navBar {
  border-radius: 0rem 0rem .2rem .2rem;
}

.navBar-logo {
  width: 50px;
  height: 50px;
}

/* Dropdown */
.dropdownFilter {
  padding-top: 5px;
  padding-bottom: 5px;
}

.dropdownFilterButton {
  transform: translate(0px, 0px);
  position: absolute;
  width: 175.5px;
  height: 27.07px;
  color: #000;
  border: 0.5px solid #C7C7C7;
  box-sizing: border-box;
  padding: 1px 6px;
}

/* Edit Button */
.editButton {
  background-color: #f0f0f0;
  border: 0.5px solid #C7C7C7;
}

.editButton-Icon {
  background-color: rgba(0, 0, 0, 0);
  border: #C7C7C7;
  color: #000;
  transition: 0.15s;
}

.editButton:hover > .editButton-Icon {
  color: white;
}

/* Total */

.totalButton {
  transform: translate(486px, 5px);
  position: absolute;
  width: 47.07px;
  height: 27.07px;
  background-color: #f0f0f0;
  border: 0.5px solid #C7C7C7;
  padding: 1px 1px;
  font-size: 10px;
  border-radius: .2rem;
}

/* Add Button */
.addButton {
  transform: translate(451px, 5px);
  position: absolute;
  width: 27.07px;
  height: 27.07px;
  background-color: #f0f0f0;
  border: 0.5px solid #C7C7C7;
  padding: 1px 1px;
  border-radius: .2rem;
}

.addButton-Icon {
  transform: translate(0.5px, -2px);
  width: 13.535px;
  height: 13.535px;
  background-color: rgba(0, 0, 0, 0);
  border: #C7C7C7;
  color: #000;
  padding: 1px 1px;
  border-radius: .2rem;
  transition: 0.15s;
}

.addButton:hover > .addButton-Icon {
  color: white;
}

.searchBar {
  position: absolute;
  transform: translate(183.5px, 0px);
  width: 50%;
  height: 27.05px;
  margin: auto;
  padding-top: 5px;
  padding-bottom: 5px;
}

.searchBarInput {
  transform: translate(0px, 0px);
  width: 245.5px;
  height: 27.07px;
  position: absolute;
  border: 0.5px solid #C7C7C7;
  border-radius: .2rem .2rem .2rem .2rem;
}

.searchBarSubmit {
  transform: translate(185.5px, 0px);
  height: 27.05px;
  position: absolute;
  border-top: 0.5px solid #C7C7C7;
  border-right: 0.5px solid #C7C7C7;
  border-bottom: 0.5px solid #C7C7C7;
  border-left: 0.5px solid #C7C7C7;
  border-radius: 0rem .2rem .2rem 0rem;
  z-index: 0;
  transition: 0.15s;
}

.searchBarSubmit:hover {
  background-color: #6c757d;
  border-color: #6c757d;
  color: white;
}

.date-searchBar {
  position: absolute;
  transform: translate(183.5px, 0px);
  width: 50%;
  height: 27.05px;
  margin: auto;
  padding-top: 5px;
  padding-bottom: 5px;
}

.date-searchBarInput-start {
  transform: translate(0px, 0px);
  width: 93.25px;
  height: 27.07px;
  position: absolute;
  border: 0.5px solid #C7C7C7;
  border-radius: .2rem 0rem 0rem .2rem;
  z-index: 1;
}
.date-searchBarInput-end {
  transform: translate(93.25px, 0px);
  width: 93.25px;
  height: 27.07px;
  position: absolute;
  border: 0.5px solid #C7C7C7;
  border-radius: 0rem 0rem 0rem 0rem;
  z-index: 1;
}

.date-searchBarSubmit {
  transform: translate(185.5px, 0px);
  height: 27.05px;
  position: absolute;
  border-top: 0.5px solid #C7C7C7;
  border-right: 0.5px solid #C7C7C7;
  border-bottom: 0.5px solid #C7C7C7;
  border-left: 0.5px solid #C7C7C7;
  border-radius: 0rem .2rem .2rem 0rem;
  z-index: 0;
  transition: 0.15s;
}

.date-searchBarSubmit:hover {
  background-color: #6c757d;
  border-color: #6c757d;
  color: white;
}

@media (max-width: 485px) {
  .dropdownFilter {
    width: 50%;
  }
  .dropdownFilterButton {
    width: 50%;
  }

  .searchBarInput {
    width: 100%;
    transform: translate(-53.5px, 0px);
  }

  .searchBarSubmit {
    transform: translate(126.5px, 0px);
  }

  .date-searchBarInput-start {
    width: 90.75px;
    transform: translate(-53.5px, 0px);
  }
  .date-searchBarInput-end {
    width: 90.75px;
    transform: translate(37.25px, 0px);
  }

  .date-searchBarSubmit {
    transform: translate(126.5px, 0px);
  }

  .addButton {
    transform: translate(390.93px, 5px);
  }
}

